          img: "https://gw.alipayobjects.com/zos/rmsportal/UTjFYEzMSYVwzxIGVhMu.png",
          category: "8",
          target: "https://www.yuque.com",
        }
      ],
    };
  },

  methods: {
    naviList(key) {
      return this.items.filter((item) => item.category === key);
    },
    openTarget(target) {
      window.open(target, "_blank");
    },
  },

  watch: {
    menu(newVal) {
      if (document.getElementById(newVal.key)) {
        window.scrollBy(
          0,
          document.getElementById(newVal.key).getBoundingClientRect().top -
            80 -
            32
        );
      }
    },
  },

  created() {
    bus.$on("menus", (val) => {
      this.menus = val;
    });

    bus.$on("menu-active", (val) => {
      this.menu = val;
    });
  },
};
</script>

